header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:30px;
  background: #000;
  color:#fff;
    a {
      margin:0 10px;
      color:#fff;
      text-decoration: none;
    }
}
main{
    width: 90%;
    margin:30px auto 0;
    input {
      width: 500px; height:30px;
      margin-left:50px;
      border:0; border-bottom: 1px solid #b7b7b7;
      &:focus{
        outline: none;
      }
    }
    > div {
      width: 100%;
      text-align: center;
      margin:30px 0;
    }
    
    button {
      border:0;
      width: 200px;
      background: #000;
      color:#fff;
      padding:10px;
    }
  section {
    display: flex;
    flex-wrap: wrap;
    div{
      width: 25%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      figure {
        p {
          img {
            width: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }
}